import React, { useEffect, useState } from "react";
import {
    Row,
    Col,
    Card,
    Button,
    InputGroup,
    FormControl,
} from "react-bootstrap";

import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import i18n from './../../i18n';

import Aux from "../../hoc/_Aux";
import CustomInput from "../../Components/CustomInput";

import InputStatus from "../../Components/InputStatus"
import Address from "../../Address/Gouvernerat"
//import MessageModal from "../../Components/Alert/Alert";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css";
var urlbackend = require("../../env/env")();

const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

const AddPickup = () => {
    const [Zones, setZones] = useState([])
    const [city, setcity] = useState([])
    const [gouvernorat, setgouvernorat] = useState('')
    // const [showMessageParams, setShowMessageParams] = useState({
    //     display: false,
    //     type: "success",
    //     message: null
    // })
    useEffect(() => {



    })




    const choosedelegeation = (value) => {
        setgouvernorat(value)
        const config = {
            headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Headers": "x-access-token",
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios
            .get(urlbackend + "/zone", config)
            .then((response) => {
                console.log("this respon list", response.data);
                setZones(response.data.data.filter(e => e.gouvernorat === value))
            })
            .catch((e) => {
                console.log(e);
            });
    }





    return (


        <Aux>
            <Row>
                {/*             {showMessageParams.display && <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />}
 */}
                <Col>
                    <Card>
                        <Card.Header>
                            <Card.Title as="h5">{i18n.t('add-package')}</Card.Title>
                        </Card.Header>
                        <Card.Body>

                            <Formik
                                initialValues={{
                                    name_complete: "",
                                    phone_number: "",
                                    code_postal: "",
                                    email: "",
                                    address: "",
                                    price: 0,
                                    type_envoi: "",
                                    designation: "",
                                    lot: 1,
                                    gouvernorat: "",
                                    payment_status: "1",
                                    payment_mode: "",
                                    zone: ""

                                }}
                                validationSchema={
                                    Yup.object().shape({
                                        name_complete: Yup.string()
                                            .min(2,i18n.t('error-short-full-name'))
                                            .required(i18n.t('error-required-full-name')),
                                        phone_number: Yup.string()
                                            .matches(phoneRegExp,i18n.t('error-phone-number'))
                                            .required(i18n.t('error-required-phone-number')),
                                        gouvernorat: Yup.string()
                                            .min(2, i18n.t('error-short-governorate'))
                                            .required(i18n.t('error-required-governorate')),
                                        //  email: Yup.string().max(255).required('Nom is required'),
                                        address: Yup.string()
                                            .required(i18n.t('error-address')),
                                        price: Yup.number()
                                            .typeError(i18n.t('error-price'))
                                            .min(0,i18n.t('error-positive-price'))
                                            .required(i18n.t('error-required-price')),
                                        type_envoi: Yup.string()
                                            .required(i18n.t('error-required-send-type')),
                                            payment_status: Yup.string()
                                            .required(i18n.t('error-required-payment-status')),
                                        payment_mode: Yup.string()
                                            .required(i18n.t('error-required-payment-mode')),
                                        designation: Yup.string()
                                            .min(2, i18n.t('error-short-designation'))
                                            .required(i18n.t('error-required-designation')),
                                        zone:Yup.string() 
                                            .required(i18n.t('error-required-zone')),
                                        lot:  Yup.number()
                                            .typeError(i18n.t('error-quantity'))
                                            .min(0,i18n.t('error-positive-quantity'))
                                            .required(i18n.t('error-required-quantity')),
                                    })
                                }
                                onSubmit={(values, { resetForm }) => {
                                    confirmAlert({
                                        title: 'Merci Confirmer Livraison de colis ?',
                                        message: '',
                                        buttons: [
                                            {
                                                label: 'Yes',
                                                onClick: () => {
                                                    const config = {
                                                        headers: {
                                                            'Content-Type': 'application/json',
                                                            'Access-Control-Allow-Headers': 'Authorization',
                                                            "x-access-token": localStorage.getItem('token'),
                                                            'Authorization': 'Bearer ' + localStorage.getItem('token'),
                                                        },
                                                    };
                                                    axios.post(urlbackend + "/pickup/one/add", { "data": [{ ...values, ...{ "city": city } }] }, config)
                                                        .then(response => {
                                                            console.log(response.data)
                                                            alert("add success")
                                                            //  window.location.href = "/datta-able/react/default/v3/AddPickup"
                                                            resetForm()
                
                                                        })
                                                        .catch(e => {
                                                            console.log(e)
                                                        })
                
                                                    // axios.post(urlpostuser + '/gestionnaire/add', {
                                                    //   lastname: values.lastName,
                                                    //   firstname: values.firstName,
                                                    //   addresse: values.addresse,
                                                    //   Phone: values.Phone,
                                                    //   email: values.email,
                                                    //   password: values.password,
                                                    //   typeroles:exercicest
                
                                                    // }).then((response) => {
                
                                                    //  if(response.data.code ==="success"){
                                                    //   alert("gestionnaire enrigstré");
                                                    //   reloadthi()
                                                    //   handleClose()
                
                                                    //  }else {
                                                    //    alert("Adresse mail existe déjà")
                                                    //  }
                
                
                
                                                    // }).catch(()=>{
                                                    //   alert("Adresse mail existe déjà")
                
                                                    // })
                
                
                
                
                                                }
                            
                                            },
                                            {
                                                label: 'No',
                                                onClick: null
                                            }
                                        ]
                                    });
                            
                            

                         

                                }}
                            >
                                {({
                                    errors,
                                    handleBlur,
                                    handleChange,
                                    handleSubmit,
                                    isSubmitting,
                                    touched,
                                    values
                                }) => (
                                    <form onSubmit={handleSubmit}>

                                        <Card>

                                            <Aux>

                                                <Row>
                                                    <Col md={6}>
                                                        <CustomInput
                                                            name="name_complete"
                                                            onChange={handleChange}
                                                            onBlur={handleBlur}
                                                            value={values.name_complete}
                                                            errors={errors.name_complete}
                                                            touched={touched.name_complete}
                                                            label={i18n.t('fullname')}
                                                        />


                                                        <CustomInput
                                                            name="phone_number"
                                                            label={i18n.t('phone-number')}
                                                            type="number"
                                                            onChange={handleChange}
                                                            onBlur={handleBlur}
                                                            value={values.phone_number}
                                                            errors={errors.phone_number}
                                                            touched={touched.phone_number}
                                                        />

                                                        {/*      <CustomInput
                                                            name="code_postal"
                                                            label="Code postal"
                                                            onChange={handleChange}
                                                            onBlur={handleBlur}
                                                            value={values.code_postal}
                                                            errors={errors.code_postal}
                                                            touched={touched.code_postal}
                                                            type="code_postal"
                                                        /> */}


                                                        <label htmlFor="basic-url">{i18n.t('governorate')} <InputStatus errors={errors.gouvernorat} touched={touched.gouvernorat} /> </label>

                                                        <InputGroup className="mb-3">
                                                            <FormControl
                                                                as="select"
                                                                id="gouvernorat"
                                                                custom
                                                                onChange={(e) => {
                                                                    handleChange(e);
                                                                    choosedelegeation(e.target.value);

                                                                }
                                                                }
                                                                onBlur={handleBlur}
                                                                value={values.gouvernorat}
                                                                errors={errors.gouvernorat}
                                                            >
                                                                <option>{i18n.t('governorate')}</option>

                                                                {Address.map((zone, index) => (
                                                                    <option onClick={(e) => choosedelegeation(e.target.value)} key={zone.Gouvernorat} value={zone.Gouvernorat}>
                                                                        {zone.Gouvernorat}
                                                                    </option>
                                                                ))}
                                                            </FormControl>
                                                        </InputGroup>
                                                        <label htmlFor="basic-url">{i18n.t('zone')}<InputStatus errors={errors.zone} touched={touched.zone} /> </label>

                                                        <InputGroup className="mb-3">
                                                            <FormControl
                                                                as="select"
                                                                id="zone"
                                                                custom
                                                                onChange={(e) => {
                                                                    handleChange(e);
                                                                    setcity(e.target.value);

                                                                }
                                                                }
                                                                onBlur={handleBlur}
                                                                value={values.zone}
                                                                errors={errors.zone}
                                                            >
                                                                <option>{i18n.t('zone')}</option>

                                                                {Zones.map((zone, index) => (
                                                                    <option onClick={(e) => setcity(Zones[index].city)} key={zone.city} value={Zones[index].city}>
                                                                        {zone.city}
                                                                    </option>
                                                                ))}
                                                            </FormControl>
                                                        </InputGroup>
                                                        <CustomInput
                                                            name="ville address"
                                                            label={i18n.t('city-delegation')}
                                                            onChange={(e) => setcity(e.target.value)}
                                                            onBlur={(e) => setcity(e.target.value)}
                                                            value={city}
                                                            errors={errors.zone}
                                                            touched={touched.zone}

                                                        />

                                                        <CustomInput
                                                            name="address"
                                                            label={i18n.t('address')}
                                                            onChange={handleChange}
                                                            onBlur={handleBlur}
                                                            value={values.address}
                                                            errors={errors.address}
                                                            touched={touched.address}
                                                        />


                                                    </Col>
                                                    <Col md={6}>
                                                        {/*     <CustomInput
                                                            name="email"
                                                            label="E-mail"
                                                            onChange={handleChange}
                                                            onBlur={handleBlur}
                                                            value={values.email}
                                                            errors={errors.email}
                                                            touched={touched.email}
                                                        /> */}
                                                        {/*   <CustomInput
                                            name="payement"
                                            label="payement"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.payement}
                                            touched={touched.payement}
                                        />  */}
                                                        <label htmlFor="basic-url">{i18n.t('payment')}<InputStatus errors={errors.payment_status} touched={touched.payment_status} /></label>

                                                        <InputGroup className="mb-3">
                                                            <FormControl
                                                                as="select"
                                                                id="payment_status"
                                                                custom

                                                                onChange={handleChange}
                                                                onBlur={handleBlur}
                                                                value={values.payment_status}
                                                            >
                                                                <option>{i18n.t('choose-payment-type')}</option>

                                                                {[{ statepay:i18n.t('paid'), valuep: "0" }, { statepay: i18n.t('not-paid'), valuep: "1" }].map((zone, index) => (
                                                                    <option key={zone.statepay} value={zone.valuep}>
                                                                        {zone.statepay}
                                                                    </option>
                                                                ))}
                                                            </FormControl>
                                                        </InputGroup>
                                                        {values.payment_status ==="1" && <CustomInput
                                                            name="price"
                                                            label={i18n.t('price')}
                                                            type="number"
                                                            onChange={handleChange}
                                                            onBlur={handleBlur}
                                                            value={values.price}
                                                            touched={touched.price}
                                                            errors={errors.price}
                                                            readOnly
                                                        />
                                                        }

                                                        <label htmlFor="basic-url">{i18n.t('payment-type')}<InputStatus errors={errors.payment_mode} touched={touched.payment_mode} /></label>

                                                        <InputGroup className="mb-3">
                                                            <FormControl
                                                                as="select"
                                                                id="payment_mode"
                                                                custom
                                                                onChange={handleChange}
                                                                onBlur={handleBlur}
                                                                value={values.payment_mode}
                                                            >
                                                                <option value="">{i18n.t('choose-payment-type')}</option>

                                                                {[i18n.t('species'), i18n.t('check'), i18n.t('check-species')].map((zone, index) => (
                                                                    <option key={zone} value={zone}>
                                                                        {zone}
                                                                    </option>
                                                                ))}
                                                            </FormControl>
                                                        </InputGroup>
                                                        <label htmlFor="basic-url">{i18n.t('send-type')}<InputStatus errors={errors.type_envoi} touched={touched.type_envoi} /></label>

                                                        <InputGroup className="mb-3">
                                                            <FormControl
                                                                as="select"
                                                                id="type_envoi"
                                                                custom
                                                                onChange={handleChange}
                                                                onBlur={handleBlur}
                                                                value={values.Formateur}
                                                            >
                                                                <option value="">{i18n.t('send-type')}</option>

                                                                {[i18n.t('home-delivery'), i18n.t('exchange'), i18n.t('recovery')].map((type_envoi, index) => (
                                                                    <option key={type_envoi} value={type_envoi}>
                                                                        {type_envoi}
                                                                    </option>
                                                                ))}
                                                            </FormControl>
                                                        </InputGroup>
                                                        <CustomInput
                                                            name="designation"
                                                            label={i18n.t('designation')}
                                                            onChange={handleChange}
                                                            onBlur={handleBlur}
                                                            value={values.designation}
                                                            touched={touched.designation}
                                                            errors={errors.designation}

                                                        />

                                                        <CustomInput
                                                            name="lot"
                                                            label={i18n.t('quantity')}
                                                            type="number"
                                                            onChange={handleChange}
                                                            onBlur={handleBlur}
                                                            value={values.lot}
                                                            touched={touched.lot}
                                                            errors={errors.lot}

                                                        />





                                                    </Col>
                                                    <Col>
                                                        <Button

                                                            type="submit"
                                                            color="info"
                                                            variant="info"
                                                        >
                                                            {i18n.t('confirm')}
                                                        </Button>
                                                    </Col>

                                                </Row>
                                            </Aux>

                                        </Card>

                                    </form>

                                )}

                            </Formik>
                        </Card.Body>
                    </Card>
                </Col>

            </Row>
        </Aux>

    );
};


export default AddPickup;

